@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap");
@import url("./Nullify.scss");
body {
  font-size: 16px;
  margin: 0;
  font-family: Roboto;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: hsl(234, 29%, 20%);
  text-align: center;
  display: flex;

  align-items: center;
  width: 100%;
  min-height: 100%;
  @media (min-width: 768px) {
    justify-content: center;
  }

  flex-direction: column;
  color: hsl(235, 18%, 26%);
}
.App {
  max-width: 1415px;
  padding: 0px 15px;
  @media (max-width: 768px) {
    padding: 0;
  }
  margin: 0 auto;
}
._container {
  padding: 0 6rem;
  @media (max-width: 768px) {
    padding: 0;
  }
}
.form {
  background-color: hsl(0, 0%, 100%);
  border-radius: 30px;

  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  @media (min-width: 768px) {
    padding: 1.5rem 1.5rem;
    flex-direction: row;
  }
  @media (max-width: 768px) {
    border-radius: 0px;
  }
  .main-part {
    padding: 6rem 4rem 4rem 4rem;
    @media (max-width: 768px) {
      padding: 4rem 2rem 2rem 2rem;
    }
    @media (max-width: 450px) {
      padding: 2rem 1rem 1rem 1rem;
    }
    display: flex;
    flex-direction: column;
    max-width: 35rem;
    align-items: start;
    text-align: start;
    line-height: 150%;

    h1 {
      font-weight: 700;
      font-size: 42px;
      margin-bottom: 2rem;
      @media (max-width: 768px) {
        margin-bottom: 1rem;
      }
    }
    h4 {
      margin-bottom: 2rem;
      @media (max-width: 768px) {
        margin-bottom: 1rem;
      }
    }
    p {
      margin-bottom: 2rem;
      @media (max-width: 768px) {
        margin-bottom: 1rem;
      }
      display: flex;
      gap: 1rem;
      align-items: center;
    }
    form {
      display: flex;
      flex-direction: column;
      text-align: start;
      width: 100%;
      label {
        margin-bottom: 2rem;
        font-weight: 700;
        @media (max-width: 768px) {
          margin-bottom: 1rem;
        }
      }
      input {
        margin-bottom: 2rem;
        width: 100%;
        @media (max-width: 768px) {
          margin-bottom: 1rem;
        }

        padding: 1rem;
        border: solid 1px hsl(231, 7%, 60%);
        border-bottom: 1px solid #fff;
        cursor: pointer;
        border-radius: 10px;
        &:focus {
          border: 1px solid hsl(235, 18%, 26%);
        }
      }
      button {
        font-weight: 700;
        color: #fff;
        background-color: hsl(235, 18%, 26%);
        width: 100%;
        padding: 1rem;
        border-radius: 10px;
        transition: all 0.2s;
        &:hover {
          background-color: hsl(4, 100%, 67%);
        }
      }
    }
  }

  .svgMobile {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}
.success {
  background-color: #fff;
  padding: 3rem 3rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  border-radius: 30px;
  svg {
    margin-bottom: 2rem;
  }
  h1 {
    font-size: 40px;
    font-weight: 700;
    text-align: start;
    margin-bottom: 2rem;
    color: hsl(235, 18%, 26%);
  }
  p {
    margin-bottom: 2rem;
    text-align: start;
    line-height: 150%;
    color: hsl(235, 18%, 26%);
  }
  button {
    font-weight: 700;
    color: #fff;
    background-color: hsl(235, 18%, 26%);
    width: 100%;
    padding: 1rem;
    border-radius: 10px;
    transition: all 0.2s;
    &:hover {
      background-color: hsl(4, 100%, 67%);
    }
  }
}
.success_content {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5rem;
  max-width: 600px;
  border-radius: 30px;
}
